.sidebar {
    /*background-color: #00cc22;*/
    height: 100vh;
    width: 200px;
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}

.background {
}

.nav {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.nav a {
    margin-top: .35rem;
    font-weight: 500;
    font-size: 1.35rem;
    color: black;
    text-decoration: none;
}

.nav a:hover {
    color: #4582c7;
}

.nav a.active {
    color: #0b5daf;
    text-decoration: #0b5daf;
}

.close {
    align-self: end;
    margin: .45rem;
    border: none;
    scale: 1.1;
}

.close img {
    /*background-color: #cc9c00;*/
    border: none;
}
