.button {
    /*width: 6.5rem;*/
    height: 2.15rem;
    border: none;
    border-radius: 5px;
    padding: .75rem 2rem;
    font-size: 1.15rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button::first-letter {
    text-transform: uppercase;
}

.disabled {
    background-color: #528ecb;
    color: rgba(245, 245, 245, 0.23);
}

.secondary {
    background-color: whitesmoke;
    color: #0066cc;
    border: 1px solid #0066cc;;
}

.default {
    background-color: #0066cc;
    color: whitesmoke;
}

.red {
    background-color: red;
    color: whitesmoke;
}

