.greetingForm {
    padding-top: 1.5rem;
}

.inputAndButtonContainer {
    display: flex;
}

.error {
    color: red;
}

.input {
    display: inline-block;
    height: 1.75rem;
    border: 1px black solid;
    border-radius: 3px;
}

.input:focus {
}

.errorInput {
    display: inline-block;
    height: 1.75rem;
    border: 1px red solid;
    border-radius: 3px;
}

.button {
    width: 3.25rem;
    height: 1.75rem;
    background-color: rgb(4, 79, 124);
    color: whitesmoke;
    border: none;
    margin-left: 10px;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    background-color: rgb(45, 153, 225, .55);
}

.text {
    font-size: .95rem;
    margin-bottom: 1rem;
}

.greeting {
    margin-top: 5px;
    font-weight: 500;
}
